	module.exports = {"data":{"post":{"html":"<p>One rule I've been following when blogging about our new business is \"no numbers\"... which is too bad, since the numbers are the second-most fun part of the business (but not nearly as much fun as the people you work with). If you're really curious, you can check at <a href=\"http://www.mcdonalds.de/\">mcdonalds.de</a> things like the average annual sales per restaurant or how much equity is required to become a franchisee.</p>\n<p>Yesterday we got our first formal financing offer, and it was somewhat breathtaking to see the monthly payment in black and white. It's one thing to have a line in your projected P&amp;L for debt service, it's quite another to have a concrete payment schedule with a payment that <em>well exceeds</em> our former gross salaries. Then the enormity of our undertaking becomes clear.</p>\n<p>Then you blink a couple of times, look at the plan again, and wonder if you're getting a good rate. Maybe the second offer will be a tenth of a percent cheaper...</p>","fields":{"title":"Shock and Awe","date":"31 May 2007","url":"https://papascott.de/2007-05-31-shock-and-awe/"}}},"pathContext":{"slug":"/2007-05-31-shock-and-awe/","prev":{"fields":{"title":"T Minus 5 Weeks","date":"28 May 2007","slug":"/2007-05-28-t-minus-5-weeks/"}},"next":{"fields":{"title":"The Blueberry Hercules","date":"31 May 2007","slug":"/2007-05-31-the-blueberry-hercules/"}}}}

